import React, { useState } from 'react'
import Button from '../../utils/Button'
import Modal from './Modal'

export default function SendFormMessage({ onClose }) {
    const [isChecked, setChecked] = useState(false);
    const handleToggleCheckbox = () => {
        setChecked(prevState => !prevState)
    }

    return (
        <Modal header='Uwaga!' onClose={onClose}>
            <p>Wysanie wiadomoci nie oznacza ustalenia daty wizyty. Nastpi to po skontaktowaniu si telefonicznie.</p>
            <p>Umawiajc na wizyt akceptujesz <u>odstpy od cennika</u> oraz <u>kwoty zwizane z dojazdem do klienta</u>.</p>

            <p>
                <label htmlFor="checkbox"><input type="checkbox" id='checkbox' checked={isChecked} onChange={handleToggleCheckbox} /> Zgadzam si na przetwarzanie danych osobowych w celach ...</label>
            </p>
            <div className='footer'>
                <span onClick={onClose}>Anuluj</span>
                <Button disabled={!isChecked}>Wylij wiadomo</Button>
            </div>
        </Modal>
    )
}
